import { Component } from "solid-js";

import "../global.d";

import shadow from "../Assets/shadow.svg";
import river from "../Assets/river.svg";
import thunder from "../Assets/thunder.svg";
import wind from "../Assets/wind.svg";
import sky from "../Assets/sky.svg";
import star from "../Assets/star.svg";

export enum Clan {
    Shadow,
    River,
    Thunder,
    Wind,
    Sky,
    Star
}

export function clanString(clan: Clan) {
    switch (clan) {
        case Clan.Shadow:
            return "Shadow Clan";
        case Clan.River:
            return "River Clan";
        case Clan.Thunder:
            return "Thunder Clan";
        case Clan.Wind:
            return "Wind Clan";
        case Clan.Sky:
            return "Sky Clan";
        case Clan.Star:
            return "Star Clan";
    }
}

interface ClanDisplayProps {
    clan: Clan | null;
}

const DISPLAY_SIZE = 128;

export const ClanDisplay: Component<ClanDisplayProps> = (props: ClanDisplayProps) => {
    return (
        <span style={`min-height: ${DISPLAY_SIZE}px;`}>
            <img
                src={shadow}
                width={`${DISPLAY_SIZE}px`}
                height={`${DISPLAY_SIZE}px`}
                style={`display: ${props.clan == Clan.Shadow ? "block" : "none"}`}
            ></img>
            <img
                src={river}
                width={`${DISPLAY_SIZE}px`}
                height={`${DISPLAY_SIZE}px`}
                style={`display: ${props.clan == Clan.River ? "block" : "none"}`}
            ></img>
            <img
                src={thunder}
                width={`${DISPLAY_SIZE}px`}
                height={`${DISPLAY_SIZE}px`}
                style={`display: ${props.clan == Clan.Thunder ? "block" : "none"}`}
            ></img>
            <img
                src={wind}
                width={`${DISPLAY_SIZE}px`}
                height={`${DISPLAY_SIZE}px`}
                style={`display: ${props.clan == Clan.Wind ? "block" : "none"}`}
            ></img>
            <img
                src={sky}
                width={`${DISPLAY_SIZE}px`}
                height={`${DISPLAY_SIZE}px`}
                style={`display: ${props.clan == Clan.Sky ? "block" : "none"}`}
            ></img>
            <img
                src={star}
                width={`${DISPLAY_SIZE}px`}
                height={`${DISPLAY_SIZE}px`}
                style={`display: ${props.clan == Clan.Star ? "block" : "none"}`}
            ></img>
        </span>
    );
};
