import { Component, createSignal, onMount } from "solid-js";

import "../global.d";

import paw from "../Assets/paw.png";
import { genName, genStarName } from "./namegen";
import { Analyze } from "./analyze";
import { Clan, ClanDisplay, clanString } from "./ClanDisplay";

const BUTTON_SIZE = 192;

const RECORD_TIME = 2048;

export const App: Component = () => {
    var recordTime = 0;

    const [recording, setRecording] = createSignal(false);
    const [clan, setClanInternal] = createSignal<Clan | null>(null);
    const [status, setStatus] = createSignal("");
    const [catName, setCatName] = createSignal("");

    var frequenciesList: Array<Uint8Array> = [];

    onMount(() => {
        var context = new AudioContext();
        var analyzer = context.createAnalyser();
        analyzer.fftSize = 2048;

        navigator.mediaDevices.getUserMedia({ audio: true, video: false }).then((stream) => {
            var mediaSource = context.createMediaStreamSource(stream);
            mediaSource.connect(analyzer);
            context.resume();
        });

        setInterval(() => {
            if (recording()) {
                let timeData = new Uint8Array(analyzer.frequencyBinCount);

                analyzer.getByteFrequencyData(timeData);

                frequenciesList.push(timeData);

                if (Date.now() - recordTime > RECORD_TIME) {
                    onRecordingEnd(context, analyzer);
                }
            }
        }, 64);
    });

    const setClan = (clan: Clan) => {
        setStatus(clanString(clan));

        if (clan == Clan.Star) {
            setCatName("Your Cat Name: " + genStarName());
        } else {
            setCatName("Your Cat Name: " + genName());
        }

        setClanInternal(clan);
    };

    const onRecordingEnd = (context: AudioContext, analyzer: AnalyserNode) => {
        var res = Analyze(context, analyzer, frequenciesList);

        setClan(res);

        frequenciesList = [];

        setRecording(false);
    };

    const onClick = () => {
        frequenciesList = [];

        recordTime = Date.now();

        setRecording(true);
    };

    return (
        <div style="position: fixed; top: 50%; left: 50%; transform: translate(-50%, -50%);">
            <div style="display: flex; flex-direction: column; align-items: center; text-align: center;">
                <h2>Sorting Paw</h2>
                <a
                    onClick={onClick}
                    style={`display: block; cursor: pointer; width: ${BUTTON_SIZE}px; height: ${BUTTON_SIZE}px; user-select: none; text-align: center;`}
                >
                    <img
                        src={paw}
                        width={`${BUTTON_SIZE}px`}
                        height={`${BUTTON_SIZE}px`}
                        style={`opacity: ${recording() ? 1.0 : 0.33}; transition: opacity 0.2s;`}
                    ></img>
                </a>

                <div style="margin-top: 12px;"></div>

                <ClanDisplay clan={clan()} />
                <div style="min-height: 128px;">
                    <p>{status()}</p>
                    <p>{catName()}</p>
                </div>
            </div>
        </div>
    );
};
