export const genName = () => {
    const f = first[Math.floor(Math.random() * first.length)];
    const l = last[Math.floor(Math.random() * last.length)];

    return f + l;
};

const starFirst = ["Grave", "Skull", "Astral", "Spirit", "Silent", "Ghost"];

const starLast = ["meow", "cloud", "star", "leaf", "tail"];

export const genStarName = () => {
    const f = starFirst[Math.floor(Math.random() * starFirst.length)];
    const l = starLast[Math.floor(Math.random() * starLast.length)];

    return f + l;
};
