import { Clan } from "./ClanDisplay";

const MIN_FREQ_HZ = 300;
const MAX_FREQ_HZ = 4000;
const MAX_READ_FREQ_HZ = 2 * MAX_FREQ_HZ;

const STAR_CUTOFF = 140;

export function Analyze(context: AudioContext, analyzer: AnalyserNode, frequenciesList: Array<Uint8Array>): Clan {
    const SAMPLE_RATE = context.sampleRate;

    const HZ_PER_BIN = SAMPLE_RATE / 2 / analyzer.frequencyBinCount;
    const MIN_BIN = Math.round(MIN_FREQ_HZ / HZ_PER_BIN);
    const MAX_BIN = Math.round(MAX_READ_FREQ_HZ / HZ_PER_BIN);
    const BIN_COUNT = MAX_BIN + 1 - MIN_BIN;

    const MAX_A_BIN = Math.round(MAX_FREQ_HZ / HZ_PER_BIN);
    const BIN_A_COUNT = MAX_A_BIN + 1 - MIN_BIN;

    var culledList: Array<Uint8Array> = [];

    frequenciesList.forEach((list) => {
        var listMaxAmplitude = 0;

        list.forEach((val, i) => {
            if (i >= MIN_BIN && i <= MAX_BIN && val > listMaxAmplitude) {
                listMaxAmplitude = val;
            }
        });

        if (listMaxAmplitude >= STAR_CUTOFF) {
            culledList.push(list);
        }
    });

    if (culledList.length < 4) {
        return Clan.Star;
    } else {
        let average = new Uint8Array(BIN_COUNT);

        for (var i = MIN_BIN; i <= MAX_BIN; i++) {
            var val = 0;

            culledList.forEach((list) => {
                val += list[i];
            });

            val /= culledList.length;

            average[i] = val;
        }

        var total = 0;

        average.forEach((val) => {
            total += val;
        });

        var weightedAverage = 0;

        average.forEach((val, i) => {
            weightedAverage += i * (val / total);
        });

        if (weightedAverage < (5 * BIN_A_COUNT) / 10) {
            return Clan.Shadow;
        } else if (weightedAverage < (7 * BIN_A_COUNT) / 10) {
            return Clan.River;
        } else if (weightedAverage < (8 * BIN_A_COUNT) / 10) {
            return Clan.Thunder;
        } else if (weightedAverage < (9 * BIN_A_COUNT) / 10) {
            return Clan.Wind;
        } else {
            return Clan.Sky;
        }
    }
}
